.ulautocompletecollection{
	border: 1px solid #000;
	background-color: #fff;
	border-radius: 3px;
	color: #000;
	padding: 0;
	font-size: 18px;
	font-weight: normal;
	width: 89%;
	position: absolute;
	z-index: 10;
}
.ulautocompletecollection li a{
	color: #000;
	width: 100%;
	padding: 10px;
}
.ulautocompletecollection li a:hover{
	text-decoration: none;
}
.ulautocompletecollection li{
	margin: 10px;
}
